.social-block {
  position: relative;

  &:hover {
    .social-block__list {
      opacity: 1;
      pointer-events: auto;
    }
  }
}

.social-block__toggler {
  @include btn-reset;
  position: relative;
  display: block;
  width: 4rem;
  height: 4rem;
  color: $c-dark;
  border-radius: 50%;

  @media (max-width: $tablet) and (orientation: portrait) {
    display: none;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    display: none;
  }

  &::before {
    @include center;
    content: "";
    width: 0;
    height: 0;
    border-radius: 50%;
    background-color: $c-light;
    border: 2px solid $c-light;
    z-index: -1;
    transition: 0.8s;

    .dom-loaded & {
      animation: growInBounceWidth 0.4s;
      width: 100%;
      height: 100%;
    }
  }

  svg {
    @include center;
    display: block;
    width: 2.3rem;
    height: 2.2rem;
  }

  &:hover,
  .social-block--active & {
    color: $c-purple;

    &::before {
      width: 112.5%;
      height: 112.5%;
    }
  }

  &:focus {
    color: $c-purple;
    outline: none;

    &::before {
      border-color: $c-purple;
    }

    & + .social-block__list {
      opacity: 1;
      pointer-events: auto;
    }
  }

  &:active,
  &:active:focus {
    color: $c-light;

    &::before {
      width: 100%;
      height: 100%;
      background-color: $c-purple;
      border-color: $c-purple;
    }
  }

  body.slide1 & {
    color: $c-purple;

    &:hover {
      color: $c-dark;
    }

    &:focus {
      color: $c-dark;

      &::before {
        border-color: $c-dark;
      }
    }

    &:active,
    &:active:focus {
      color: $c-light;

      &::before {
        background-color: $c-dark;
        border-color: $c-dark;
      }
    }
  }

  body.slide2 & {
    color: $c-blue;

    &:hover {
      color: $c-lightblue;
    }

    &:focus {
      color: $c-lightblue;

      &::before {
        border-color: $c-lightblue;
      }
    }

    &:active,
    &:active:focus {
      color: $c-light;

      &::before {
        background-color: $c-lightblue;
        border-color: $c-lightblue;
      }
    }
  }

  body.slide3 & {
    color: $c-lightblue;

    &:hover {
      color: $c-blue;
    }

    &:focus {
      color: $c-blue;

      &::before {
        border-color: $c-blue;
      }
    }

    &:active,
    &:active:focus {
      color: $c-light;

      &::before {
        background-color: $c-blue;
        border-color: $c-blue;
      }
    }
  }
}

.social-block__list {
  @include list-reset;
  position: absolute;
  top: calc(100% - 1rem);
  right: 0;
  margin-top: 1.5rem;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.4s;

  @media (min-width: $stop-scaling) {
    margin-top: 15px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    position: static;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 0;
    opacity: 1;
    pointer-events: auto;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    position: static;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 0;
    opacity: 1;
    pointer-events: auto;
  }

  &::before {
    content: "";
    position: absolute;
    bottom: 100%;
    left: 0;
    right: 0;
    height: 1.5rem;

    @media (min-width: $stop-scaling) {
      height: 15px;
    }
  }

  &:hover {
    opacity: 1;
    pointer-events: auto;
  }

  &:focus-within {
    opacity: 1;
    pointer-events: auto;
  }

  .social-block--active & {
    opacity: 1;
    pointer-events: auto;
    top: 100%;
  }

  li {
    flex: none;
    margin-bottom: 0.5rem;

    @media (min-width: $stop-scaling) {
      margin-bottom: 5px;
    }

    @media (max-width: $tablet) and (orientation: portrait) {
      margin: 0 1.25rem;
    }

    @media (max-width: $mobile) and (orientation: landscape) {
      margin: 0 1.25rem;
    }
  }
}

.social-block__link {
  position: relative;
  display: block;
  width: 4rem;
  height: 4rem;
  top: 1rem;
  color: $c-light;
  border: 2px solid transparent;
  border-radius: 50%;
  opacity: 0;
  transition: opacity 0.4s;

  .social-block--active & {
    top: 0;
    opacity: 1;
    transition-property: color, top, opacity;
    transition-timing-function: ease, ease, ease-in;
    transition-duration: 0.4s;
  }

  @media (min-width: $stop-scaling) {
    width: 40px;
    height: 40px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    color: $c-light;
    background-color: $c-dark;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    color: $c-light;
    background-color: $c-dark;
  }

  &:hover {
    color: $c-purple;
  }

  &:focus {
    color: $c-purple;
    background-color: $c-light;
    border-color: $c-purple;
    outline: none;
  }

  &:active,
  &:active:focus {
    color: $c-purple;
    background-color: $c-light;
    border-color: $c-light;
  }

  svg {
    @include center;
  }

  &--fb {
    svg {
      width: 0.8rem;
      height: 1.7rem;

      @media (min-width: $stop-scaling) {
        width: 8px;
        height: 17px;
      }
    }
  }

  &--insta {
    .social-block--active & {
      transition-delay: 0s, 0.2s, 0.2s;
    }

    svg {
      width: 1.5rem;
      height: 1.5rem;

      @media (min-width: $stop-scaling) {
        width: 15px;
        height: 15px;
      }
    }
  }

  &--vk {
    .social-block--active & {
      transition-delay: 0s, 0.4s, 0.4s;
    }

    svg {
      width: 1.7rem;
      height: 1rem;

      @media (min-width: $stop-scaling) {
        width: 10px;
        height: 10px;
      }
    }
  }
}

@keyframes growInBounceWidth {
  0% {
    width: 0;
    height: 0;
  }

  60% {
    width: 120%;
    height: 120%;
  }

  100% {
    width: 100%;
    height: 100%;
  }
}
