@keyframes flamingoFlight {
  0% {
    transform: rotate(0) translateY(0);
  }
  50% {
    transform: rotate(5deg) translateY(3%);
  }
  50% {
    transform: rotate(-3deg) translateY(8%);
  }
  100% {
    transform: rotate(0) translateY(0);
  }
}
@keyframes flamingoFadeUp {
  0% {
    transform: rotate(-30deg) translate3d(-30%, 100%, 0);
    opacity: 0;
  }
  100% {
    transform: rotate(0) translate3d(0, 0, 0);
    opacity: 1;
  }
}
@keyframes caseScaleUp {
  0% {
    transform: scale(0);
    bottom: -20%;
  }
  70% {
    transform: scale(1);
    bottom: 18%;
  }
  100% {
    transform: scale(1);
    bottom: 16%;
  }
}
@keyframes leafRightSwing {
  0% {
    opacity: 0;
    transform: rotate(-20deg);
  }
  17% {
    opacity: 1;
    transform: rotate(15deg);
  }
  34% {
    transform: rotate(-6deg);
  }
  51% {
    transform: rotate(3deg);
  }
  68% {
    transform: rotate(-4deg);
  }
  85% {
    transform: rotate(2deg);
  }
  100% {
    opacity: 1;
    transform: rotate(0);
  }
}
@keyframes leafLeftSwing {
  0% {
    opacity: 0;
    transform: rotate(20deg);
  }
  17% {
    opacity: 1;
    transform: rotate(-15deg);
  }
  34% {
    transform: rotate(6deg);
  }
  51% {
    transform: rotate(-3deg);
  }
  68% {
    transform: rotate(4deg);
  }
  85% {
    transform: rotate(-2deg);
  }
  100% {
    opacity: 1;
    transform: rotate(0);
  }
}
@keyframes rollIn {
  0% {
    opacity: 0;
    transform: translateX(100%) rotate(180deg);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}
